.parent {
    width: 100vw;
    height: auto; /* Allow content to grow */
    min-height: 100vh;
    background-color: black;
    margin: 0;
    overflow: hidden;
    position: relative;
    overflow-y: auto; /*Allow vertical scrolling if needed */
    background-image: url('./images/yourBackgroundImage6.jpg');
    background-size: cover; /* Make sure the background covers the entire container */
    background-position: center; /* Center the image within the container */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-attachment: fixed;
    color: whitesmoke;
}

.image {
    aspect-ratio: 1.3/2;
    height: 400px;
    object-fit: cover;
    object-position: 100% 50%;
}

.section {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.shadowbox {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: .25s;
}

.sectionname {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
    bottom: 40px;
    font-size: 24px;
    font-weight: 500;
    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.7);
    transition: .25s;
}

.section:hover .sectionname {
    bottom: 50%;
    transform: translate(-50%, 50%);
    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.7);
    transition: .25s;
}

.section:hover .shadowbox {
    box-shadow: inset 0 0 300px 0 rgba(255, 255, 255, .5);
    transition: .25s;
}

.track {
    display: flex;
    gap: 20px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    user-select: none;
    transition: .5s;
}

.bgimg {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    transition: .5s;
    position: fixed;
}

.bluredbg {
    filter: blur(10px);
    transition: 2s;
}

.expandedsection {
    opacity: 0;
    position: absolute;
    height: 0px;
    width: 0px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transition: 0.75s ease-in-out;
    /* transition-property: margin-right; */
}

.expandedcontainer {
    width: 100%;
    height: 100%;
    position: relative;
    overflow-y: auto;
    top: 0;
    left: 0;
}








